import React, { Component } from 'react';

// class component
class Table extends Component {
    render () {
        const { cellData, removeCharacter } = this.props;

        return (
            <table>
                <THead />
                <TBody
                    cellData={cellData}
                    removeCharacter={removeCharacter}
                />
            </table>
        );
    }
}

const THead = () => {
    return (
        <thead>
            <tr>
                <th>Name</th>
                <th>Job</th>
                <th>Remove</th>
            </tr>
        </thead>
    );
}

const TBody = props => {
    const rows = props.cellData.map((row, index) => {
        return (
            <tr key={index}>
                <td>{row.name}</td>
                <td>{row.job}</td>
                <td><button onClick={() => props.removeCharacter(index)}>Delete</button></td>
            </tr>
        );
    });
    
    return (
        <tbody>{rows}</tbody>
    );
}

export default Table;